@import "../../inc/vars";
.acro-faq-block {
	padding: 56px 20px 40px 20px;
	@media (min-width: $screen-sm){
		padding: 56px 40px 40px 40px;
	}
	@media (min-width: $screen-mlg){
		width: 1100px;
		max-width: 100%;
		margin-left: auto;
		margin-right: auto;
	}
	@media (min-width: $screen-lg){
		padding: 56px 100px 80px 100px;
	}
	h3 {
		text-align: center;
		color: #5A6272;
		@include pop(400, 24, 1.24);
		margin-bottom: 12px;
		@media (min-width: $screen-sm){
			text-align: left;
		}
	}
	h2 {
		text-align: center;
		margin-bottom: 40px;
		@include pop(400, 34, 1.24);
		@include titleGrad();
		@media (min-width: $screen-sm){
			text-align: left;
			font-size: 36px;
		}
	}
	.acro-faq__content {
		@media (min-width: $screen-md){
			padding-left: 60px
		}
		&__items {
			.list-item {
				padding: 20px 0 0 20px;
				position: relative;
				&:last-child {
					border: none;
				}
				@media (min-width: $screen-sm){
					padding-left: 60px;
				}
				&__title {
					color: $purple;
					@include pop(400, 18, 1.36);
					position: relative;
					text-align: left;
					@media (min-width: $screen-sm){
						@include pop(400, 22, 1.36);
						margin-bottom: 10px;
					}
					&:before {
						content: '';
						display: block;
						width: 8px;
						height: 8px;
						border-radius: 50%;
						background: $purple;
						position: absolute;
						left: -24px;
						top: 50%;
						transform: translateY(-50%);
						@media (min-width: $screen-sm){
							left: -60px;
						}
					}
				}
				&__description {
					@include fig(400, 16, 1.56);
					padding-bottom: 20px;
					@media (min-width: $screen-sm){
						font-size: 18px;
						border-bottom: 1px #D1D5DB solid;
					}
				}
				&:last-child {
					.list-item__description {
						border-bottom: none;
					}
				}
			}
		}
	}
}